<template>
  <div class="answer" oncopy="return false;">
    <span class="title">机器人答案</span>
    <div style="height:25px"></div>
    <div class="gm-scroll-view">
      <ul class="answer-view">
        <li v-for="(a,index) in newArray" :key="index">
          <div>
            <span class="number">{{index+1}}、</span>
            <div>
              <span class="question" :title="a.q.length>12 ?  a.q:'' ">问题：{{a.q.length>12 ? a.q.substring(0,12)+'...'  : a.q}}</span>
              <span class="q_answer" :title="a.a.replace('<br/>','').length>12 ?  a.a.replace('<br/>',''):''">答案：<span>{{a.a.length>12 ? a.a.substring(0,12)+'...'  : a.a}}</span></span>
            </div>
          </div>
          <div>
            <span class="edit" :data-text="a.a" :robot_uu_id="a.robot_uu_id" :dialogId=" a.dialogId" @click="edit($event)">查看编辑</span>
            <span class="sent_btn" :data-text="a.a" :robot_uu_id="a.robot_uu_id" :dialogId=" a.dialogId" @click="sent($event)">发送</span>
          </div>
        </li>
      </ul>
    </div>
    <span class="title">流程指引</span>
    <div style="height:25px"></div>
    <div class="gm-scroll-view" style="height:55%;">
      <ul class="one">
        <li v-for="(a,index) in newArray" :key='index' class="one-li">
           <ul class="two">
             <li v-for="(b,index_) in a.pAnswer" :key='index_'>
                <div class="recommended">
                  <div class="productBtn" @click="showHide(index_,index)" v-show="a.display_name">{{a.display_name}}</div>
                  <!-- <span class="unfoldBtn" >{{indexNum == index?'展开':'折叠'}}</span> -->
                </div>
                <!-- <div class="recommended">
                  <span class="productBtn">{{a.display_name}}</span>
                  <span class="unfoldBtn" @click="Fold(index)">{{indexNum == index?'折叠':'展开'}}</span>
                </div> -->
               <ul class="three" :ref="index_">
                 <li v-for="(c,index__) in b" :key='index__' @click="sentProcess($event)" :title='c.content'>
                    流程{{index__+1}}：{{c.content.length>12 ? c.content.substring(0,12)+'...'  : c.content}}
                 </li>
               </ul>
             </li>
           </ul>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import Bus from '../../bus.js';

  export default {
    name: 'answer',
    props: {
      targetIds: {
        type: Object,
      },
      index: {
        type: Number
      }
    },
    data() {
      return {
        indexNum:'',
        processPproduct:''
      }
    },
    computed: {
      newArray: function () {
        if (this.targetIds.qa_record && this.targetIds.qa_record.length > 0) {
          var qa_record = this.targetIds.qa_record;
          var answers = this.targetIds.answers;
          for (var i = 0; i < qa_record.length; i++) {
//              qa_record[i].a = qa_record[i].a .replace(/<br\/>/g,'')
            for (var j = 0; j < answers.length; j++) {
//                answers[j].a = answers[j].a.replace(/<br\/>/g,'')
              if (qa_record[i].q == answers[j].q && qa_record[i].sentTime == answers[j].sentTime) {
                answers.splice(j, 1);
                j--;
              }
            }
          }
          return this.targetIds.qa_record.concat(this.targetIds.answers)
        } else {
          console.log(this.targetIds.answers)
          return this.targetIds.answers
        }
      },
      answer: function () {
        return this.targetIds.answer
      }
    },
    watch: {
      newArray: {
        handler(newValue, oldValue) {
          var this_ = this;
          this.$nextTick(function () {
            setTimeout(function () {
              var div = document.getElementsByClassName('gm-scroll-view');
              div[this_.index].scrollTop = div[this_.index].scrollHeight;
            }, 200)
          })
        },
        deep: true,
        immediate: true,
      },
      answer: {
        handler(newValue, oldValue) {
          var this_ = this;
          this.$nextTick(function () {
            setTimeout(function () {
              var div = document.getElementsByClassName('gm-scroll-view');
              div[this_.index].scrollTop = div[this_.index].scrollHeight;
            }, 200)
          })
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      edit(event) {
        var this_ = this;
        var target = event.target
        this.robot_balance(target.getAttribute("robot_uu_id"), target.getAttribute("dialogId"));
        Bus.$emit('look', {data_text: target.getAttribute("data-text").replace(/<br\/>/g, '\n'), 'index': this_.index})
      },
      sent(event) {
        var this_ = this;
        var target = event.target
        this.robot_balance(target.getAttribute("robot_uu_id"), target.getAttribute("dialogId"));
        Bus.$emit('sent', {data_text: target.getAttribute("data-text"), 'index': this_.index})
      },
      sentProcess(event) {
        var this_ = this;
        console.log(event)
        var target = event.target
        console.log(target)
        // this.robot_balance(target.getAttribute("robot_uu_id"), target.getAttribute("dialogId"));
        Bus.$emit('sentProcess', {data_text: target.title, 'index': this_.index})
      },

      robot_balance(robot_uu_id, dialogId) {
        this.$ajax({
          method: "post",
          url: "/acs/v1.0/robot_balance",
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          data: {
            'robot_uu_id': robot_uu_id,
            'dialogId': dialogId
          },
          transformRequest: [function (data) {
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
        }).then(res => {

        })
      },
      Spread(index) {
        this.indexNum = index
        this.processPproduct = true
      },
      Fold(index) {
        console.log(index)
        this.indexNum = index
        this.processPproduct = false
      },
      showHide(index_,index) {
        console.log(index)
        console.log(index_)
        this.indexNum = index
        // var index = 1
        // console.log(this.$refs)
        // console.log(this.$refs[index])
        // console.log(this.$refs[index][0])
        // console.log(this.$refs[index][0].style)
        // console.log(this.$refs[index][0].style.display)
        if (this.$refs[index_][index].style.display=="none") {
          this.$refs[index_][index].style.display=""
        }else if (this.$refs[index_][index].style.display=="") {
          this.$refs[index_][index].style.display="none"
        }
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  body {
    margin 0
    .answer {
      border 1px solid #d6d6d6
      border-right none
      display none
      float left
      width 300px
      padding-top 20px
      height 100%
      box-sizing border-box
      background: #eef3f6;
      border-radius: 5px;
      // box-shadow: 0px 0px 16px 0px rgba(61, 104, 169, 0.17);
      border-radius: 0px;
      overflow hidden
      position relative
      left -13px
      .title {
        margin-left 20px
        font-size: 20px;
        font-family: MicrosoftYaHei;
        font-weight: bold;
        color: rgba(69, 69, 69, 1);
      }
      .answer-view {
        margin 0px 0 0 0
        padding-left 8px;
        li {
          padding 7px 0px 7px 6px
          background: rgba(255, 255, 255, 1);
          box-shadow: 0px 0px 17px 0px #cad7ea
          border-radius: 5px;
          margin-bottom: 0px;
          margin-top 12px;
          cursor pointer
          &:hover {
            background: #d0e1ed
            box-shadow: 0px 0px 8px 0px rgba(61, 104, 169, 0.17);
            border-radius: 5px;
          }
          div {
            overflow auto
            .number {
              float left
              height 100%
              font-size: 16px;
              font-family: ArialMT;
              color: rgba(49, 153, 224, 1)
            }
            .number + div {
              width 222px
              float left
            }
            .question {
              line-height 24px
              font-size 15px
              word-break break-all
            }
            .q_answer {
              display block
              font-size 14px
              color #666666
            }
            .edit:hover, .sent_btn:hover {
              background: rgba(49, 153, 224, 1);
              color white
            }
            .edit, .sent_btn {
              float right
              margin-right 20px
              color rgba(49, 153, 224, 1)
              padding 3px 5px
              font-size 14px
              margin-top 10px
              border 1px solid rgba(49, 153, 224, 1)
              cursor pointer
              border-radius: 5px;
            }
          }
        }
      }
    }
  }
  .answer .gm-scroll-view {
    overflow-y: scroll;
    overflow-x: hidden;
    margin-top: -10px;
    margin-bottom: 20px;
    width: calc(100% + 20px);
    height 35%
    .one{
        width 100%
        min-height 0px
        background: #eef3f6;
        .one-li{
          background: #eef3f6;
          border-radius: 5px;
          margin-bottom: 0px;
          height auto
          cursor pointer
        }
    }
    .two{
        width 100%
        min-height 0px
        background: #eef3f6;
        li{
          .recommended {
            width: 80px
            min-height: 0px;
            display block
            .productBtn {
              display block
              margin-left: 12px;
              margin-top:4px;
              color: #3199e0;
              padding: 3px 5px;
              font-size: 14px;
              border: 1px solid #3199e0;
              cursor: pointer;
              /* background: rgba(49,153,224,1); */
              border-radius: 5px;
              position relative
              &:hover {
                background: rgba(49, 153, 224, 1);
                color white
              }
            }
          }
        }
    }
    .three{
        width 100%
        min-height 0px
        font-size 14px
        color #666666
        background: #eef3f6;
        li{
          padding 7px 0px 7px 6px
          background: #fff;
          margin-top 8px;
          margin-bottom:8px;
          margin-left 8px
        }
    }
  }
</style>